* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik Distressed, cursive;
  list-style: none;
}

body {
  z-index: -1;
  width: 100%;
  height: 100vh;
  background: url("wpapers-min.1eb478b5.jpg") center / cover no-repeat;
  top: 0;
  left: 0;
}

section {
  width: 100%;
  height: 100vh;
  background: rgba(48, 50, 56, .5);
  justify-content: center;
  align-items: center;
  display: flex;
}

a {
  position: fixed;
  top: 15px;
  left: 10px;
}

.form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

label {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
  display: flex;
}

input[type="number"] {
  width: 200px;
  height: 30px;
  color: #70050a;
  border: 1px solid #70050a;
  border-radius: 5px;
  margin-top: 5px;
  padding: 5px;
  font-size: 16px;
}

button[type="submit"] {
  color: #fff;
  width: 150px;
  height: 40px;
  cursor: pointer;
  background: #70050a;
  border: none;
  border-radius: 10px;
  margin-top: 20px;
  font-size: 16px;
}

button[type="submit"]:hover {
  background: #920a14;
}

